a, table button, .text-link {
  background: none;
  border: none;
  text-decoration: underline;
  padding: 0;
  color: #cc0077;
  cursor: hand; }

.text-link-group .button-separator,
table .button-separator {
  color: #d8d0d5; }
  .text-link-group .button-separator:after,
  table .button-separator:after {
    content: "|"; }

button {
  background: -webkit-gradient(linear, left top, left bottom, from(#00cc55), to(#00b84c));
  background: -webkit-linear-gradient(#00cc55, #00b84c);
  background: -o-linear-gradient(#00cc55, #00b84c);
  background: linear-gradient(#00cc55, #00b84c);
  padding: 6px;
  color: white;
  border: 1px solid #008638;
  margin: 0 5px;
  border-radius: 3px;
  font-size: 13px; }

button:last-child {
  margin-right: 0; }

button:first-child {
  margin-left: 0; }

fieldset {
  padding: 10px 0 !important;
  border: none !important;
  margin: 10px 10px; }

fieldset fieldset {
  margin: 10px 0; }

fieldset > legend {
  width: 100%;
  color: #4b3a44;
  border-bottom: 1px solid #4b3a44; }

table {
  border-collapse: collapse;
  border: 1px solid #efecee;
  color: #4b3a44; }

td:last-child > button:last-child, th:last-child > button:last-child {
  margin-right: 0; }

td:last-child, th:last-child {
  text-align: right; }

th, td {
  padding: 10px; }

th, tfoot td, thead {
  font-size: 13px;
  background: #efecee;
  border: #efecee;
  font-weight: 500;
  text-transform: uppercase; }

label > input {
  display: block; }

label {
  font-size: 13px;
  color: #4b3a44; }

.error {
  color: red; }



