.code-editor {
    font-family: Consolas, 'Liberation Mono', Menlo, Courier, monospace;
    white-space: pre-wrap;
}

.style-editor-container, .code-editor  {
    width: 100%;
    height: 100%;
    min-height: 100%;
}

.style-editor-container button {
    float: right;
}


